'use client';
import { useEffect, useState } from 'react';
import apiClient from '../../lib/api-client';

export default function UsersPage() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [roles, setRoles] = useState([]);
  const [sites, setSites] = useState([]);
  const [roleFilter, setRoleFilter] = useState('');
  const [siteFilter, setSiteFilter] = useState('');
  const [nameQ, setNameQ] = useState('');
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(null);

  const [editing, setEditing] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);

  async function load() {
    setLoading(true); setError(null);
    try {
      const params = new URLSearchParams();
      if (nameQ) params.set('q', nameQ);
      if (roleFilter) params.set('role', roleFilter);
      if (siteFilter) params.set('site', siteFilter);
      // include pagination params so backend returns meta
      params.set('page', String(page));
      params.set('pageSize', String(pageSize));
      const url = '/users' + (params.toString() ? `?${params.toString()}` : '');
      const data = await apiClient(url);
      // backend may return either an array or { data, meta }
      if (Array.isArray(data)) {
        setUsers(data);
        setTotal(null);
      } else if (data?.data) {
        setUsers(data.data || []);
        setTotal((data.meta && Number(data.meta.total)) || 0);
        // sync page/pageSize in case server adjusted them
        if (data.meta?.page) setPage(Number(data.meta.page));
        if (data.meta?.pageSize) setPageSize(Number(data.meta.pageSize));
      } else {
        const rows = data ?? [];
        setUsers(Array.isArray(rows) ? rows : []);
        setTotal(null);
      }
      // derive role and site lists
      const rset = Array.from(new Set((users || []).map(r => r.role).filter(Boolean)));
      const sset = Array.from(new Set((users || []).map(r => r.site).filter(Boolean)));
      setRoles(rset);
      setSites(sset);
    } catch (err) {
      console.error('load users', err);
      setError(err?.message || 'Failed to load users');
    } finally { setLoading(false); }
  }

  useEffect(() => { load(); }, [roleFilter, siteFilter, nameQ, page, pageSize]);

  function openCreate() { setEditing({ name:'', email:'', role:'technician', site:'', nipp:'' }); setModalOpen(true); }
  function openEdit(u) { setEditing(u); setModalOpen(true); }

  async function save() {
    if (!editing) return;
    try {
      // client-side validation (email optional)
      if (!editing.name || !editing.nipp) return alert('Name and NIPP are required');
      if (editing.id) {
        await apiClient(`/users/${encodeURIComponent(editing.id)}`, { method: 'PATCH', body: editing });
      } else {
        await apiClient('/users', { method: 'POST', body: editing });
      }
      setModalOpen(false);
      setEditing(null);
      // go to first page after create/update
      setPage(1);
      await load();
    } catch (err) {
      alert(err?.body?.message || err?.message || 'Save failed');
    }
  }

  async function remove(id) {
    if (!confirm('Hapus user ini?')) return;
    try {
      await apiClient(`/users/${encodeURIComponent(id)}`, { method: 'DELETE' });
      await load();
    } catch (err) {
      alert(err?.body?.message || err?.message || 'Delete failed');
    }
  }

  return (
    <div style={{ padding:16 }}>
      <div style={{ display:'flex', justifyContent:'space-between', alignItems:'center', marginBottom:12 }}>
        <h2>Users</h2>
        <div>
          <button onClick={openCreate}>Create User</button>
          <button onClick={load} style={{ marginLeft:8 }} disabled={loading}>Refresh</button>
        </div>
      </div>

      <div style={{ display:'flex', gap:8, marginBottom:12, alignItems:'center' }}>
        <input placeholder="Search name or email" value={nameQ} onChange={e=>setNameQ(e.target.value)} style={{ padding:8, minWidth:220 }} />
        <select value={roleFilter} onChange={e=>setRoleFilter(e.target.value)} style={{ padding:8 }}>
          <option value="">All roles</option>
          {roles.map(r=> <option key={r} value={r}>{r}</option>)}
        </select>
        <select value={siteFilter} onChange={e=>setSiteFilter(e.target.value)} style={{ padding:8 }}>
          <option value="">All sites</option>
          {sites.map(s=> <option key={s} value={s}>{s}</option>)}
        </select>
        <button onClick={()=>load()} style={{ padding:8 }}>Apply</button>
      </div>

      <div style={{ display:'flex', gap:8, marginBottom:12, alignItems:'center' }}>
        <input type="file" id="user-import-file" accept=".xlsx,.xls,.csv" />
        <button onClick={async ()=>{
          const el = document.getElementById('user-import-file');
          let file = null;
          if (el && el.files) file = el.files[0];
          if (!file) return alert('Select a file first');
          const fd = new FormData();
          fd.append('file', file);
          try {
            const API_BASE = (process.env.NEXT_PUBLIC_API_URL || 'http://localhost:4000/api');
            const url = `${API_BASE.replace(/\/$/, '')}/users/import`;
            const token = typeof window !== 'undefined' ? (localStorage.getItem('token') || '').trim() : '';
            const headers = token ? { Authorization: `Bearer ${token}` } : {};
            const res = await fetch(url, { method: 'POST', body: fd, headers });
            const text = await res.text();
            let json = null;
            try { json = text ? JSON.parse(text) : null; } catch (e) { json = null; }
            if (!res.ok) {
              const msg = json?.message || text || res.statusText;
              throw new Error(msg);
            }
            alert('Import finished: ' + (json?.results?.length ?? 0) + ' rows processed');
            await load();
          } catch (err) {
            console.error('import failed', err);
            alert('Import failed: ' + (err?.message || err));
          }
        }}>Import Users</button>
      </div>

      {loading && <div>Loading...</div>}
      {error && <div style={{ color:'red' }}>{String(error)}</div>}

      <div style={{ overflowX:'auto' }}>
        <table style={{ width:'100%', borderCollapse:'collapse' }}>
          <thead>
            <tr>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>Name</th>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>NIPP</th>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>Email</th>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>Role</th>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>Site</th>
              <th style={{ textAlign:'left', padding:8, borderBottom:'1px solid #ccc' }}>Action</th>
            </tr>
          </thead>
          <tbody>
            {users.map((u) => (
              <tr key={u.id}>
                <td style={{ padding:8, borderBottom:'1px solid #eee' }}>{u.name}</td>
                <td style={{ padding:8, borderBottom:'1px solid #eee' }}>{u.nipp ?? '-'}</td>
                <td style={{ padding:8, borderBottom:'1px solid #eee' }}>{u.email}</td>
                <td style={{ padding:8, borderBottom:'1px solid #eee' }}>{u.role}</td>
                <td style={{ padding:8, borderBottom:'1px solid #eee' }}>{u.site ?? '-'}</td>
                <td style={{ padding:8, borderBottom:'1px solid #eee', display:'flex', gap:8 }}>
                  <button onClick={()=>openEdit(u)}>Edit</button>
                  <button onClick={()=>remove(u.id)}>Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* pagination controls */}
      <div style={{ marginTop:12, display:'flex', justifyContent:'space-between', alignItems:'center' }}>
        <div>
          {total !== null && (
            <div>Showing {(Math.min((page - 1) * pageSize + 1, total) || 0)} - {Math.min(page * pageSize, total)} of {total}</div>
          )}
        </div>
        <div style={{ display:'flex', gap:8, alignItems:'center' }}>
          <button onClick={()=>{ if (page>1) setPage(p=>p-1); }} disabled={page<=1}>Prev</button>
          <div>Page {page}</div>
          <button onClick={()=>{ if (total === null) { setPage(p=>p+1); } else if (page * pageSize < total) { setPage(p=>p+1); } }} disabled={total !== null && page * pageSize >= total}>Next</button>
          <select value={pageSize} onChange={e=>{ setPageSize(Number(e.target.value)); setPage(1); }} style={{ padding:6 }}>
            {[10,25,50,100].map(n => (<option key={n} value={n}>{n} / page</option>))}
          </select>
        </div>
      </div>

      {/* Modal */}
      {modalOpen && editing && (
        <div style={{ position:'fixed', left:0, right:0, top:0, bottom:0, background:'rgba(0,0,0,0.5)', display:'flex', justifyContent:'center', alignItems:'center', zIndex:2000 }}>
          <div style={{ background:'white', padding:20, width:480, borderRadius:8 }}>
            <h3>{editing.id ? 'Edit User' : 'Create User'}</h3>
            <div style={{ display:'flex', flexDirection:'column', gap:8 }}>
              <label>
                Name
                <input value={editing.name} onChange={e=>setEditing({...editing, name: e.target.value})} style={{ width:'100%', padding:8 }} />
              </label>
              <label>
                Email
                <input value={editing.email} onChange={e=>setEditing({...editing, email: e.target.value})} style={{ width:'100%', padding:8 }} />
              </label>
              <label>
                NIPP (unique)
                <input value={editing.nipp||''} onChange={e=>setEditing({...editing, nipp: e.target.value})} style={{ width:'100%', padding:8 }} />
              </label>
              <label>
                Role
                <input list="role-list" value={editing.role||''} onChange={e=>setEditing({...editing, role: e.target.value})} style={{ width:'100%', padding:8 }} />
                <datalist id="role-list">
                  {roles.map(r => (<option key={r} value={r} />))}
                </datalist>
              </label>
              <label>
                Site
                <input list="site-list" value={editing.site||''} onChange={e=>setEditing({...editing, site: e.target.value})} style={{ width:'100%', padding:8 }} />
                <datalist id="site-list">
                  {sites.map(s => (<option key={s} value={s} />))}
                </datalist>
              </label>
            </div>

            <div style={{ marginTop:12, display:'flex', justifyContent:'flex-end', gap:8 }}>
              <button onClick={()=>{ setModalOpen(false); setEditing(null); }}>Cancel</button>
              <button onClick={save}>{editing.id ? 'Save' : 'Create'}</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
